.n-pane {
	&__inner {
		height: 100%;
		padding: 20px;
		overflow-y: auto;
		overflow-x: hidden;
		&.is-disabled {
			// opacity: 0.5
			pointer-events: none;
			@include nosel;
			.n-pane__overlay {
				opacity: 0.8;
				visibility: visible; } } }
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		opacity: 0;
		visibility: hidden;
		background: #434c55;
		transition: opacity 0.15s, visibility 0.15s; } }


.n-columns-layout {
	position: relative;
	min-width: 1180px;
	height: 100%;
	@include clr; }

.color-red {
	color: #f00; }
.color-green {
	color: #0b860b; }

.buttons-center {
	text-align: center;
	.form-buttons > {
		.btn,
		.btn:last-child {
			margin: 0 5px; } } }

.i-title {
	padding: 9px 0 10px;
	margin-bottom: 20px;
	color: #999;
	font-size: 24px;
	text-align: center; }

.n-link {
	display: inline-block;
	vertical-align: top;
	color: $blue;
	font-size: inherit;
 }	// text-decoration: underline

.n-add {
	a {
		display: inline-block;
		vertical-align: top;
		color: $blue;
		font-size: 16px;
		text-decoration: underline; }
	i {
		margin-right: 8px; } }

.n-back {
	i {
		margin-right: 8px; } }

.n-close {
	padding: 0;
	border: none;
	color: $red;
	background: none;
	i {
		font-size: 16px; } }

.n-btn-toggle {
	position: relative;
	display: inline-block;
	vertical-align: top;
	padding: 15px 15px 15px 15px;
	border: none;
	width: 175px;
	color: $blue;
	background: none;
	font-size: 16px;
	text-align: left;
	&.is-active {
		.n-btn-toggle {
			&__show {
				display: none; }
			&__hide {
				display: inline-block; }
			&__arrow {
				transform: rotate(180deg); } } }
	&__show {
		display: inline-block; }
	&__hide {
		display: none; }
	&__show,
	&__hide {
		vertical-align: top; }
	&__arrow {
		display: inline-block;
		vertical-align: top;
		margin-left: 5px;
		font-size: 22px;
		@include gpu;
		transition: transform 0.175s ease-in-out; } }


//containers
.n-section {
	position: relative;
	padding: 20px;
	&_step {
		padding: 0 0 93px;
		height: 100%; }
	&__body {
		padding: 20px;
		height: 100%;
		overflow: auto; }
	&__container {
		margin: 0 auto;
		// max-width: 800px
		max-width: 600px;
		&_ordering {
			max-width: 720px; } }
	&__footer {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 20px;
		text-align: center;
		.form-buttons {
			margin: 0;
			> .btn,
			> .btn:last-child {
				margin: 0 5px; } } }
	&__radios {
		max-width: 600px; }
	&__tabs {
		margin: 0 auto 40px;
		max-width: 600px;
		&.tab-control > .tab {
			margin: 0 2px 0 0; } }
	.tab-elemtn {
		width: 33.33%; }
	&__alert {
		position: absolute;
		top: 20px;
		left: 20px;
		right: 20px;
		font-size: 14px;
		text-align: left; } }

.n-box {
	padding-bottom: 20px; }

.n-label {
	display: block;
	&_title {
		font-size: 16px;
		margin-bottom: 15px; } }

.n-radio-container {
	padding-bottom: 10px;
	.radio {
		margin-top: 0; } }

.n-vertical-center {
	height: 100%;
	@include vertical;
	&__content {
		display: inline-block;
		vertical-align: middle;
		width: 100%; } }

//modifiers styles
.n-modifiers {
	.pane-left {
		background: #f6f4f4;
		width: 40%; }
	.pane-right {
		margin-left: 40%;
		overflow-y: auto;
		overflow-x: hidden; }
	.list {
		padding: 0;
		margin: 20px 0;
		list-style: none;
		background: white;
		.item {
			position: relative;
			padding: 10px 10px 10px 20px;
			margin-top: -1px;
			border: 1px solid #eaeaea;
			&:before {
				content: "\2261";
				position: absolute;
				left: 7px;
				color: silver; } } } }

//product modifiers
.n-modifiers-container {
	max-width: 650px; }
.n-modifiers-section {
	margin-bottom: 20px;
	@include clr;
	&__btn {
		padding-right: 20px; }
	&__slash {
		padding-right: 20px;
		line-height: 53px; } }
.n-modifiers-block {
	&__title {
		padding: 10px 0;
		margin-bottom: 20px;
		border-bottom: 2px solid #eee;
		color: $blue;
		font-size: 20px; } }

#modifierReportTable {
	min-width: 1700px; }
#ingredientsReportTable {
	min-width: 1300px; }

//ingredients ordering
#taxTable input[type="text"],
#pricingTable input[type="text"],
#orderingTable input[type="text"] {
	width: 100%; }

//store transfer
.store-transfer {
	height: 100%; }
.store-transfer-buttons {
	margin-top: 30px; }
.i-ingredients-table {
	min-width: 1400px; }

//header
.pane-header {
	&__right {
		display: none; }
	.pane-header__left {
		.toggle-options-panel-button {
			margin-right: 30px; } }
	&_double {
		min-width: 1180px;
		@include clr;
		.pane-header__left {
			width: 40%; }
		.pane-header__right {
			display: block;
			width: 60%; }
		.pane-header__left,
		.pane-header__right {
			position: relative;
			float: left;
			height: 100%; }
		.pane-header {
			&__back {
				position: relative;
				z-index: 1;
				float: left;
				padding: 0 20px;
				border: none;
				height: 100%;
				color: $white;
				background: none;
				font-size: 16px;
				i {
					margin-right: 4px;
					font-size: 24px;
					line-height: 50px; }
				i, span {
					display: inline-block;
					vertical-align: top; } }
			&__subtitle {
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				text-align: center; } } } }
