.s-tabs {
	position: relative;
	// padding-top: 60px
	padding: 80px 20px 20px;
	height: 100%;
	&__header {
		position: absolute;
		// top: 0
		// left: 0
		// right: 0
		top: 20px;
		left: 20px;
		right: 20px; }
	&__list {
		padding: 0;
		margin: 0;
		font-size: 0;
 }		// background: #eee
	&__item,
	&__btn {
		display: inline-block;
		vertical-align: top; }
	&__item {
		margin-bottom: -2px; }
	&__btn {
		position: relative;
		min-width: 100px;
		padding: 20px 20px;
		color: #333;
		font-size: 16px;
		line-height: 20px;
		text-align: center;
		text-decoration: none !important;
		@include nosel;
		&.is-active {
			background: $white;
			&:before {
				opacity: 1; } }
		&.is-disabled {
			color: rgba($black, 0.3);
			pointer-events: none; }
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: -1px;
			border: 1px solid #e5e5e5;
			border-bottom-color: $white;
			opacity: 0; } }
	&__body {
		overflow: auto;
		border: 1px solid #e5e5e5;
		height: 100%; }
	&__content {
		display: none;
		padding: 25px 40px 40px;
		&.is-open {
			display: block; } } }

