.n-module {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	height: 100%;
	background: $white;
	transform: translate3d(100%,0,0);
	transition: transform 0.4s ease-in-out;
	&.is-open {
		@include gpu; }
	&_filters {
		// padding-top: 191px
		.n-module__container {
			padding-top: 244px; } }
	&_filters-only {
		.n-module__container {
			padding-top: 191px; } }
	&_simple {
		.n-module__container {
			padding-top: 93px; } }
	&_simplest {
		.n-module__container {
			padding-top: 20px; } }
	&__header {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		padding: 20px; }
	// &__back
	// 	float: left
	// 	width: 120px
	// 	min-width: 120px
	// 	max-width: 120px
	&__title {
		overflow: hidden;
		padding: 9px 0 10px;
		color: #999;
		font-size: 24px;
		text-align: left; }
	&__description {
		padding-bottom: 20px;
		font-size: 14px; }
	&__search,
	&__filters {
		padding-top: 20px; }
	&__body {
		height: 100%; }
	&__container {
		position: relative;
		padding-top: 166px;
		margin: 0 auto;
		max-width: 800px;
		height: 100%; }
	&__create {
		float: right; } }

